import React from 'react';
import {Link} from 'react-scroll'
import './App.css';

class Navbar extends React.Component {
  render() {
    let navNum = "Navbar Nav" + this.props.num;
    return (
      <div className={navNum} id = {this.props.id}>
        <div className = "NavLeftBlock"/>
        <NavItem text = "Home" goTo = "nav1" setPage = {this.props.setPage}/>
        <NavItem text = "About" goTo = "nav2" setPage = {this.props.setPage}/>
        <NavItem text = "Contact" goTo = "nav3" setPage = {this.props.setPage}/>
      </div>
    )
  }
}

class NavItem extends React.Component {
  handleSetActive = () => this.props.setPage(this.props.text)
  render() {
    return (
      <div className = "NavItem">
        <Link activeClass="Active"
              to={this.props.goTo}
              spy={true}
              smooth= "easeInOutCubic"
              duration={900}
              onSetActive={this.handleSetActive}
              >
          {this.props.text}
        </Link>
      </div>
    )
  }
}

class LandingPage extends React.Component {
  render() {
    return (
      <div className = "LandingPage ScreenHigh">
        <div className = "LandingTopBlock"/>
        <Tagline line="web developer"/>
        <div className = "LandingBotBlock"/>
      </div>
    )
  }
}

class About extends React.Component {
  render() {
    return (
      <div className = "About">
        <ul className = "AboutLeftBlock">
          <li>Hi!</li>
          <li>I'm Manuj, a second year maths and computer science student at Imperial College London</li>
          <li>I'm currently Vice President of ICL Hindu Society</li>
          <li>I love building things that will have real impact in the world.</li>
          <li>Download my resume <
              a href = "https://github.com/ManujMishra2000/Resume/blob/master/Resume.pdf">here</a>
          </li>
        </ul>
        <div className = {`SuitPic ${this.props.mainName}`}/>
        <div className = {`London ${this.props.londonName}`} />
        <div className = {`Taj ${this.props.tajName}`} />
      </div>
    )
  }
}

function Tagline(props) {
  return (
    <div className = "Tagline Tagfont">
      {props.line}
    </div>
  )
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {currentPage: "Home"};
    this.changePage = this.changePage.bind(this);
  }

  changePage(pageName) {
    this.setState({
      currentPage: pageName
    })
  }

  render() {
    let mainName = this.state.currentPage === "About" ? "Appear" : "Transparent";
    let londonName = this.state.currentPage === "About" ? "SlideLeft" : "Transparent";
    let tajName = this.state.currentPage === "About" ? "SlideRight" : "Transparent";
    return (
      <div className="All ScreenWide BackColour">
        <Navbar num = "1" id = "nav1" setPage = {this.changePage}/>
        <LandingPage/>
        <Navbar num = "2" id = "nav2" setPage = {this.changePage}/>
        <About mainName = {mainName} londonName = {londonName} tajName = {tajName}/>
        <Navbar num = "3" id = "nav3" setPage = {this.changePage}/>
        <div className = "Contact">
          <p>Email me at: manujmishra2000@gmail.com</p>
        </div>
      </div>
    )
  }
}

export default App;
